import React from 'react'
import Tab from './Tab'
import { connect } from 'react-redux'

function Shoucang(props) {
  console.log(props)
  return (
    <div>
      <div>收藏的宝贝</div>
      <button onClick={props.history.goBack}>返回</button>
      <div>
        {
          props.shoucang.map((item, idx) => (
            <li key={idx}>
              {item}
              <button
                onClick={() => {
                  props.dispatch({
                    type: "DEL_TODO",
                    index: idx
                  })
                }}
              >删除</button>
            </li>
          ))
        }
      </div>
      <Tab />
    </div>
  )
}
const mapStateToProps = (state) => {
  console.log(state)
  return { shoucang: state.todo }
}
export default connect(mapStateToProps)(Shoucang)
