import React, { useEffect } from 'react'
import './detail.css'
import { connect } from 'react-redux'
import { getlist } from './actionCreators'

const Detail = (props) => {
    console.log({ props })
    return (
        <div style={{ display: 'inline-block' }}>
            <div style={{ fontSize: '30px' }} onClick={props.history.goBack}>&lt;</div>
            <div>
                <img src={props.location.idx} />
                <p>{props.location.i}</p>
                <p>{props.location.id}</p>
            </div>
            <div style={{ position: 'absolute', bottom: '0' }}>
                <button class='btn1'>
                    <i className='iconfont icon-shouye1'></i><br />
                    店铺
                </button>
                <button class='btn1'>
                    <i className='iconfont icon-kefu1'></i><br />
                    客服
                </button>
                <button class='btn1'
                    onClick={()=>props.dispatch({
                        type:"ADD_TODO",
                        value:`${props.location.i}`
                    })}
                >
                    <i className='iconfont icon-shoucang1'></i><br />
                    收藏
                </button>
                <button class='btn2 btn'>
                    <i className='iconfont icon-gouwuche'></i><br />
                    加入购物车
                </button>
                <button class='btn2'>
                    <i className='iconfont icon-tubiaozhizuomoEban'></i><br />
                    立即购买
                </button>
            </div>
        </div>
    )
}

export default connect()(Detail);