import React, { useEffect } from 'react'
// import { NavLink } from 'react-router-dom'
import Lb from './Lb'
import './home.css'
import Tab from './Tab'
import { connect } from 'react-redux'
import { getlist } from './actionCreators'

const Home = (props) => {
	//派发action，存入store
	useEffect(() => {
		props.dispatch(getlist())
	}, [])
	console.log(props);
	return (<div class='l'>
		<div class='head'>
			<span>淘</span>
			<input placeholder='寻找宝贝店铺' class='sear'
				style={{ marginLeft: '10px', textAlign: 'center' }}
				onClick={() => props.history.push('/Search')}
			></input>
		</div>
		<Lb />
		<div style={{ color: 'rgb(215,25,123)', textAlign: 'center', marginTop: '40px' }}
		>--*猜你喜欢*--</div>
		<div class='boxs'>
			{props.goods.map(d => (
				<div class='box' 
				// onClick={()=>props.history.push({pathname:'/detail', idx:`${d.img}`})}>
				onClick={()=>props.history.push({pathname:'/detail', idx:`${d.img}`,i:`${d.title}`,id:`${d.price}`})}>
					<img src={d.img}/>
					<p>{d.title}</p>
					<p>{d.price}</p>
				</div>
			))}
		</div>
		<Tab/>
	</div>)
}

const mapStateToProps = (state) => {
	console.log(state)
	return { goods: state.home }
}
export default connect(mapStateToProps)(Home);


















